import React, { Component } from "react";
import { Link } from "react-router-dom";
import {
  Row,
  Col,
} from "reactstrap";

import whitelogo from "../assets/images/white-logo.png";
import samplebot from "../assets/images/samplebot.png";
import samplebot1 from "../assets/images/samplebot1.png";
import samplebot2 from "../assets/images/samplebot2.png";

import Container from "reactstrap/lib/Container";

class Initiate extends Component {
    render() {
        return (
            <React.Fragment>
                <header className="main-bg-color my-header">
                    <Row className="navbar-header">
                        <Col lg="6" className="my-header-logo">
                            <Link to="/dashboard">
                                <span className="logo-sm">
                                    <img src={whitelogo} alt="" className="h-100"/>
                                </span>
                            </Link>
                        </Col>
                        <Col lg="6" className="text-white">
                            <Row>
                                <Col lg="2" className="text-center">
                                <Link className="my-header-text text-center" to="/my-bots">
                                    My Bots
                                </Link>
                                </Col>
                                <Col lg="3" className="text-center">
                                <Link className="my-header-text text-center" to="/bot-map">
                                    Bot Map
                                </Link>
                                </Col>
                                <Col lg="3" className="text-center">
                                <Link className="my-header-text text-center" to="#">
                                    <b><u>Initiate a Bot!</u></b>
                                </Link>
                                </Col>
                                <Col lg="4" className="text-center">
                                <Link className="my-header-text text-center" to="/bot-exchange">
                                    Bot Exchange
                                </Link>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                </header>
                <div className="main-bg-color">
                    <div className="d-flex justify-content-center pt-5">
                        <img src={samplebot2} className="init-bot" alt="..."/>
                        <img src={samplebot1} className="init-bot" alt="..."/>
                        <img src={samplebot} className="init-bot" alt="..."/>
                    </div>
                    <div className="d-flex justify-content-center">
                        <button className="init-button">
                            Initiate Bot!
                        </button>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default Initiate;
