import React, { Component } from "react";
import { Link } from "react-router-dom";
import {
  Row,  Col
} from "reactstrap";

import whitelogo from "../assets/images/white-logo.png";
import samplebot from "../assets/images/samplebot3.png";

class BotMap extends Component {
    render() {
        return (
            <React.Fragment>
                <header className="main-bg-color my-header">
                    <Row className="navbar-header">
                        <Col lg="6" className="my-header-logo">
                            <Link to="/dashboard">
                                <span className="logo-sm">
                                    <img src={whitelogo} alt="" className="h-100"/>
                                </span>
                            </Link>
                        </Col>
                        <Col lg="6" className="text-white">
                            <Row>
                                <Col lg="2" className="text-center">
                                    <Link className="my-header-text text-center" to="/my-bots">
                                        My Bots
                                    </Link>
                                </Col>
                                <Col lg="3" className="text-center">
                                    <Link className="my-header-text text-center" to="#">
                                        <b><u>Bot Map</u></b>
                                    </Link>
                                </Col>
                                <Col lg="3" className="text-center">
                                    <Link className="my-header-text text-center"  to="/initiate-bots">
                                        Initiate a Bot!
                                    </Link>
                                </Col>
                                <Col lg="4" className="text-center">
                                    <Link className="my-header-text text-center" to="/bot-exchange">
                                        Bot Exchange
                                    </Link>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                </header>
                <Row className="main-bg-color m-0">
                    <Col lg="6"></Col>
                    <Col lg="6">
                        <center>
                            <span className="botmap-title-txt">BotMap</span>
                        </center>
                    </Col>
                </Row>
                <Row className="main-bg-color m-0">
                    <Col lg="5">
                        <center>
                            <span>
                                <img src={samplebot} className="botmap-bot" alt="..."/>
                            </span>
                        </center>
                    </Col>
                    <Col lg="7">
                        <Row>
                            <div className="w-50 float-left">
                                <p className="q-title">
                                    Q1 - Launch
                                </p> 
                                <p className="q-text">
                                    &#183; Giveaways and promotion. <br />
                                    &#183; Initiate all 10,000 Bots. <br />
                                    &#183; Reveal rarity of each trait. 
                                </p>
                            </div> 
                        </Row>
                        <div className="w-100" style={{height:"150px"}}>
                            <div className="w-50 float-right">
                                <p className="q-title">
                                    Q2 - Bot Exchange
                                </p> 
                                <p className="q-text">
                                    &#183; Release of the Bot Exchange 
                                    <br /> &nbsp; for all Bot Owners to use.
                                </p>
                            </div>
                        </div>
                        <div>
                            <p className="q-title">
                                Q3 - Upgrade Vault
                            </p> 
                            <div>
                                <p className="q-text">
                                    &#183; Realease of PCU Upgrade Chips. <br />
                                    &#183; Initial sneak peak at 
                                    &nbsp;BILLIONAIRE BOTS (Supply=50)
                                </p>
                            </div>
                            
                        </div>
                        <div className="w-100" style={{height:"150px"}}>
                            <div className="w-50 float-right">
                                <p className="q-title">
                                    Q4 - Future
                                </p> 
                                <p className="q-text">
                                    &#183; Look into partnerships with huge NFT projects <br /> &nbsp; and  real  world brands. 
                                    <br /> &#183; Develop the project into new and bigger <br /> &nbsp; ventures such as  crypto gaming.
                                </p>
                            </div>
                        </div>
                        
                    </Col>
                </Row>
            </React.Fragment>
        );
    }
}

export default BotMap;
